  .homepagebtn {
    display: flex;
    justify-content: space-between;
}
.button-59 {
  align-items: center;
  background-color: #fff;
  border: 1px solid #000;
  box-sizing: border-box;
  color: #000;
  cursor: pointer;
  display: inline-flex;
  fill: #000;
  font-family: Inter,sans-serif;
  font-size: 16px;
  font-weight: 400;
  height: 40px;
  justify-content: center;
  /* letter-spacing: -.8px; */
  line-height: 24px;
  /* min-width: 100px; */
  outline: 0;
  padding: 0 17px;
  text-align: center;
  text-decoration: none;
  transition: all .3s;
  user-select: none; 
  min-width: fit-content !important;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-59:focus {
  color: #171e29;
}

.button-59:hover {
  border-color: rgb(179, 0, 0);
  color: rgb(227, 0, 0);
  fill: rgb(186, 0, 0);
}

.button-59:active {
  border-color: #06f;
  color: #06f;
  fill: #06f;
}

@media (min-width: 768px) {
  .button-59 {
    min-width: 100px;
  }
}
.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
